










import Vue from "vue";
import Component from "vue-class-component";
import { Unites } from "./enums";

const UniteFieldProps = Vue.extend({
  props: {
    unite: String,
    label: {
      type: String,
      default: "Unit"
    },
    disabled: {
      type: Boolean,
      default: false
    },
    unites: {
      type: Array,
      default: function() {
        return Unites;
      }
    }
  },
  model: {
    prop: "unite",
    event: "change"
  }
});

@Component({})
export default class UniteField extends UniteFieldProps {}
